export default {
	name: "tuiButton",
	props: {
		//样式类型 primary, white, danger, warning, green,blue, gray，black
		type: {
			type: String,
			default: 'primary'
		},
		//是否加阴影
		shadow: {
			type: Boolean,
			default: false
		},
		// 宽度 rpx或 %
		width: {
			type: String,
			default: '100%'
		},
		//高度 rpx
		height: {
			type: String,
			default: '94rpx'
		},
		//字体大小 rpx
		size: {
			type: Number,
			default: 32
		},
		//形状 circle(圆角), square(默认方形)，rightAngle(平角)
		shape: {
			type: String,
			default: 'square'
		},
		plain: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		loading: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		handleClick() {
			if (this.disabled) {
				return false;
			}
			this.$emit('click', {})
		},
		getShadowClass: function(type, shadow,plain) {
			let className = '';
			if (shadow && type != 'white' && !plain) {
				className = 'tui-shadow-' + type;
			}
			return className;
		},
		getDisabledClass: function(disabled, type) {
			let className = '';
			if (disabled && type != 'white' && type != 'gray') {
				className = 'tui-dark-disabled';
			}
			return className;
		},
		getShapeClass: function(shape, plain) {
			let className = '';
			if (shape == 'circle') {
				className = plain ? 'tui-outline-fillet' : 'tui-fillet';
			} else if (shape == "rightAngle") {
				className = plain ? 'tui-outline-rightAngle' : 'tui-rightAngle';
			}
			return className;
		},
		getHoverClass: function(disabled, type, plain) {
			let className = '';
			if (!disabled) {
				className = plain ? 'tui-outline-hover' : ('tui-' + (type || 'primary') + '-hover');
			}
			return className;
		}
	}
}
